
import {Vue} from "vue-class-component";
import { Emit } from 'vue-property-decorator'
import axios from 'axios';

export default class ModalSingup extends Vue{

  cadastro = {
    name: null,
    email: null,
    date: null,
    password: null,
    cpf: null,
    phone: null,
  }

  checkbox = false;

  @Emit("close")
  close(){
    return false;
  }

  private axiosInstace = axios.create({
    baseURL: 'https://localhost:5001/api/v1/'
  })

    onSubmit(){
      if(!this.checkbox) {
        window.alert("Aceite os termos para se cadastrar !");
      }
      else {
        window.localStorage.setItem ('cadastro', JSON.stringify (this.cadastro));
        window.localStorage.setItem ('login', '1')
        document.location.reload (true);
        return new Promise(
            () => {
              this.axiosInstace.post('/aluno', this.cadastro).then(
                  () => {
                    this.cadastro = {
                      name: null,
                      email: null,
                      date: null,
                      password: null,
                      cpf: null,
                      phone: null,
                    };
                  }
              )
            });
      }

  }

  onReset () {
    this.cadastro = {
      name: null,
      email: null,
      date: null,
      password: null,
      cpf: null,
      phone: null,
    }
  }
  mounted(){
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }
}
